<template>
  <loading :active="isLoading"></loading>
  <div>
    <div class="d-flex justify-content-between">
      <h2><i class="bi bi-ticket-perforated"></i> 折價券列表</h2>
      <button
        class="btn btn-primary text-end"
        type="button"
        @click="openModal(true)"
      >
        新增折價券
      </button>
    </div>
    <table class="table mt-4 text-center border">
      <thead class="table-dark">
        <tr>
          <th>名稱</th>
          <th>折扣百分比</th>
          <th>到期日</th>
          <th>是否啟用</th>
          <th>編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in coupons" :key="key">
          <td>{{ item.title }}</td>
          <td>{{ item.percent }}%</td>
          <td>{{ $filters.date(item.due_date) }}</td>
          <td>
            <span v-if="item.is_enabled === 1" class="text-success">啟用</span>
            <span v-else class="text-muted">未啟用</span>
          </td>
          <td>
            <div class="btn-group">
              <button
                class="btn btn-outline-success "
                @click="openModal(false, item)"
              >
                編輯
              </button>
              <button class="btn btn-outline-danger "
              @click="openDelModal(item)">刪除</button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
  <CouponModal
    ref="couponModal"
    :coupon="tempCoupon"
    @update-coupon="updateCoupon"
  ></CouponModal>
  <DelModal 
  :item="tempCoupon"
  ref="delModal"
  @del-item="delCoupon"
  ></DelModal>
</template>
<script>
import CouponModal from "@/components/back/CouponModal.vue";
import DelModal from "@/components/back/DelModal.vue";
export default {
  data() {
    return {
      coupons: {},
      pagination: {},
      tempCoupon: {
        title: "",
        is_enabled: 0,
        percent: 100,
        code: "",
      },
      isLoading:false,
      isNew: false,
    };
  },
  inject: ['emitter','$httpMessageState'],
  components: {
    CouponModal,
    DelModal,
  },
  methods: {
    getCoupons(page = 1) {
      this.isLoading=true;
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupons/?page=${page}`;
      this.$http.get(api).then((res) => {
        this.isLoading=false;
        // console.log(res);
        this.coupons = res.data.coupons;
        this.pagination = res.data.pagination;
      });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempCoupon = {
          due_date: new Date().getTime() / 1000,
        };
      } else {
        this.tempCoupon = { ...item };
      }
      const couponComponent = this.$refs.couponModal;
      couponComponent.showModal();
    },
    updateCoupon(tempCoupon) {
      // console.log(item);
      // 新增
      if (this.isNew) {
        this.isLoading=false;
        const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupon`;
        this.$http.post(url, { data: tempCoupon }).then((response) => {
          // console.log(response, tempCoupon);
          this.isLoading=true;
          this.$httpMessageState(response, '新增優惠券');
          this.getCoupons();
          this.$refs.couponModal.hideModal();
        });
      } else {
        this.isLoading=false;
        const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupon/${this.tempCoupon.id}`;
        this.$http.put(url, { data: this.tempCoupon }).then((response) => {
          // console.log(response);
          this.isLoading=true;
          this.$httpMessageState(response, '更新優惠券');
          this.getCoupons();
          this.$refs.couponModal.hideModal();
        });
      }
    },
    openDelModal(item) {
      this.tempCoupon = { ...item };
      const delComponent = this.$refs.delModal;
      delComponent.showModal();
    },
    delCoupon() {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupon/${this.tempCoupon.id}`;
      this.isLoading = true;
      this.$http.delete(api).then((response) => {
        this.isLoading = false;
        this.$httpMessageState(response, '刪除優惠券');
        const delComponent = this.$refs.delModal;
        delComponent.hideModal();
        this.getCoupons();
      });
    },
  },
  created() {
    this.getCoupons();
  },
};
</script>
